import React from 'react';
import { connect } from 'react-redux';
import playerService from 'src/services/player';
import store, { RootState } from 'src/@store';
import {
  paused,
  playing,
  play,
  loadError,
  dataLoaded,
  waiting,
  loadSilent,
  loadNextPart,
  MainPlayerState,
} from 'src/@slices/player/mainPlayer';
import { Mix } from 'src/models/mix';
import { app } from 'src/utils/config';
import { getSource } from 'src/utils/drive';
import { getNextPart, hasNextPart, isMultipart, isPlaylist } from 'src/utils/mix';

const {
  onLoadStart,
  onLoadedData,
  onPause,
  onPlay,
  onPlaying,
  onError,
  onWaiting,
  onAbort,
  onEnded,
} = playerService;

type Subscription = {
  unsubscribe: CallableFunction;
};

const { dispatch } = store;
const { storageKey, lastMixStorageKey, partStorageKey } = app('HISTORY');

type Props = {
  historyLength: number;
  historyPointer: number | null;
  history: Mix[];
  isSilent: boolean;
  isPaused: boolean;
  currentMix: Mix | null;
  mainPlayer: MainPlayerState;
};

class PlayerBackendProvider extends React.Component<Props, any> {
  private onLoadStart$?: Subscription;
  private onLoadedData$?: Subscription;
  private onPlay$?: Subscription;
  private onPause$?: Subscription;
  private onPlaying$?: Subscription;
  private onWaiting$?: Subscription;
  private onError$?: Subscription;
  private onAbort$?: Subscription;
  private onEnded$?: Subscription;

  public componentWillUnmount() {
    this.onLoadStart$?.unsubscribe();
    this.onLoadedData$?.unsubscribe();
    this.onPlay$?.unsubscribe();
    this.onPause$?.unsubscribe();
    this.onPlaying$?.unsubscribe();
    this.onWaiting$?.unsubscribe();
    this.onError$?.unsubscribe();
    this.onAbort$?.unsubscribe();
    this.onEnded$?.unsubscribe();
  }

  public componentDidMount() {
    const { historyLength } = this.props;
    this.setEvents();
    if (historyLength) this.setHistory();
  }

  public componentDidUpdate({
    historyLength: prevHistoryLength,
    historyPointer: prevHistoryPointer,
  }: Props) {
    const { historyLength, historyPointer } = this.props;
    if (historyLength !== prevHistoryLength || historyPointer !== prevHistoryPointer) {
      this.saveHistory();
    }
  }

  private onLoadStartHandler = () => {
    console.log('load start');
  };

  private onLoadedDataHandler = () => {
    dispatch(dataLoaded());
    const { isSilent } = this.props;
    if (!isSilent) playerService.play();
  };

  private onPauseHandler = () => {
    dispatch(paused());
    console.log('pause');
  };

  private onPlayingHandler = () => {
    console.log('playing');
    dispatch(playing());
  };

  private onPlayHandler = () => {
    dispatch(play());
    console.log('play');
  };

  private onErrorHandler = (e: any) => {
    dispatch(loadError());
    console.log('error', e);
  };
  private onAbortHandler = (e: any) => {
    console.log('abort', e);
  };
  private onWaitingHandler = () => {
    console.log('waiting');
    dispatch(waiting());
  };
  private onEndedHandler = () => {
    console.log('ended');
    const { currentMix, mainPlayer } = this.props;
    if (!currentMix) return;

    const { mixType, parts } = currentMix;
    const { partNumber: currentPartNumber } = mainPlayer;
    if (
      (isMultipart(mixType) || isPlaylist(mixType)) &&
      hasNextPart(parts, currentPartNumber)
    ) {
      this.nextPart(currentMix);
    }
  };

  private nextPart(mix: Mix) {
    const { mainPlayer } = this.props;
    const { partNumber: currentPartNumber } = mainPlayer;
    const nextPart = getNextPart(mix.parts, currentPartNumber);
    if (!nextPart) return;

    const { partNumber: nextPartNumber } = nextPart;
    const sourceUrl = getSource(mix, nextPartNumber);
    if (!sourceUrl) return;

    dispatch(loadNextPart(nextPartNumber));
    playerService.load(sourceUrl);
    sessionStorage.setItem(partStorageKey, String(nextPartNumber));
  }

  private setEvents() {
    this.onLoadStart$ = onLoadStart.subscribe(this.onLoadStartHandler);
    this.onLoadedData$ = onLoadedData.subscribe(this.onLoadedDataHandler);
    this.onWaiting$ = onWaiting.subscribe(this.onWaitingHandler);
    this.onPause$ = onPause.subscribe(this.onPauseHandler);
    this.onPlay$ = onPlay.subscribe(this.onPlayHandler);
    this.onPlaying$ = onPlaying.subscribe(this.onPlayingHandler);
    this.onError$ = onError.subscribe(this.onErrorHandler);
    this.onAbort$ = onAbort.subscribe(this.onAbortHandler);
    this.onEnded$ = onEnded.subscribe(this.onEndedHandler);
  }

  private saveHistory() {
    const { historyPointer, history = [] } = this.props;
    sessionStorage.setItem(storageKey, JSON.stringify(history));
    sessionStorage.setItem(lastMixStorageKey, String(historyPointer));
  }

  private setHistory() {
    const { historyPointer, history = [] } = this.props;
    const indexOfLastMix = historyPointer ?? history.length - 1;
    const mix = history[indexOfLastMix];
    dispatch(loadSilent(mix));
  }

  public render = () => null;
}

const mapStateToProps = ({ playerHistory, mainPlayer }: RootState) => ({
  historyLength: playerHistory.length,
  history: playerHistory.history,
  historyPointer: playerHistory.pointer,
  isSilent: mainPlayer.isSilent,
  isPaused: mainPlayer.isPaused,
  currentMix: mainPlayer.currentMix,
  mainPlayer,
});

export default connect(mapStateToProps)(PlayerBackendProvider);
