import { MULTIPART, PLAYLIST } from 'src/constants/mix';
import { Mix, MixPart } from 'src/models/mix';

export const isMultipart = (type?: number): boolean => MULTIPART === type;

export const isPlaylist = (type?: number): boolean => PLAYLIST === type;

export const hasNextPart = (parts: MixPart[] = [], current?: number): boolean => {
  if (!current) return false;
  return getNextPart(parts, current) !== undefined;
};

export const getNextPart = (
  parts: MixPart[] = [],
  index: number = 1
): MixPart | undefined => parts[index];

export const findPartBySeconds = (
  parts: MixPart[],
  seconds: number
): MixPart | undefined => {
  let totalDuration = 0;
  return parts.find(({ duration }) => {
    totalDuration += duration;
    const low = totalDuration - duration;
    return seconds >= low && seconds <= totalDuration;
  });
};

export const sumDurationOfPartsTo = (
  part: MixPart,
  parts: MixPart[] = [],
  exclude = false
) => {
  const size = parts.findIndex(({ sourceId }) => sourceId === part.sourceId);
  return parts.slice(0, size).reduce((acc: number, { duration }: MixPart) => {
    return acc + duration;
  }, 0);
};

export const getDuration = (mix?: Mix | null, partNumber?: number): number => {
  let duration;
  if (mix && !isPlaylist(mix.mixType)) {
    duration = mix.duration;
  } else if (partNumber && mix?.parts) {
    const part = mix?.parts.find((part) => part.partNumber === partNumber);
    console.log('part', part);
    duration = part && part.duration
  }
  return duration ?? 0;
};
