import { Mix } from 'src/models/mix';
import { useSelector, useDispatch } from 'react-redux';
import {
  MainPlayerState,
  loadStart,
  loadSilent,
  loadNextPart,
} from 'src/@slices/player/mainPlayer';
import usePlayerHistory from 'src/hooks/usePlayerHistory';
import { AppStore, RootState } from 'src/@store';
import player from 'src/services/player';
import { getSource } from 'src/utils/drive';
import { findPartBySeconds, isMultipart, sumDurationOfPartsTo } from 'src/utils/mix';
import container from 'src/services/container';

const usePlayer = () => {
  const dispatch = useDispatch();
  const {
    saveSeconds,
    getSeconds,
    savePart,
    push: pushToHistory,
    back: dispatchBack,
    forward: dispatchForward,
    history,
    pointer,
  } = usePlayerHistory();
  const {
    partNumber,
    currentMix,
    isPlaying,
    isPaused,
    isLoading,
    hasError,
    isVisible,
    isSilent,
  } = useSelector<RootState, MainPlayerState>((state) => state.mainPlayer);

  const seek = (seconds: number, mix?: Mix) => {
    if (!mix) return;
    const { mixType } = mix;

    if (isMultipart(mixType)) {
      const { getState } = container.get<AppStore>('store');
      const { mainPlayer } = getState();
      const { partNumber: currentPartNumber } = mainPlayer;
      const { parts = [] } = mix;
      const part = findPartBySeconds(parts, seconds);
      if (!part) return;

      const { partNumber: nextPartNumber } = part;
      const currentTime = sumDurationOfPartsTo(part, parts, true);
      seconds -= currentTime;

      if (currentPartNumber !== nextPartNumber) {
        dispatch(loadNextPart(nextPartNumber));
        const sourceUrl = getSource(mix, nextPartNumber);
        if (sourceUrl) player.load(sourceUrl);
        savePart(nextPartNumber);
      }
    }

    player.audio.currentTime = seconds;
  };

  const pause = () => console.log('');
  const play = (mix?: Mix | null, startAt: number = 0) => {
    if (
      // When An error occured when mix playing.
      (mix && currentMix && hasError) ||
      // When the currentMix does not come
      // but coming another mix from play function as an argument.
      (!currentMix && mix) ||
      // When currentMix and mix are correct but not same mix.
      (currentMix && mix && mix.id !== currentMix.id) ||
      // When a mix does come from play function and isSilent variable is true.
      // This means that silent mode must be disabled.
      (mix && isSilent)
    ) {
      dispatch(loadStart(mix));
      pushToHistory(mix);
      const source = getSource(mix);
      if (source) player.load(source);
      seek(startAt, mix);
      saveSeconds(startAt);
      savePart(1);
    } else {
      isPlaying && player.pause();
      isPaused && player.play();
    }
  };

  const applyHistory = (mix: Mix) => {
    if (!mix) return;

    seek(0);
    saveSeconds(0);

    if (isSilent || isPaused) {
      dispatch(loadSilent(mix));
    } else {
      play(mix);
      dispatch(loadStart(mix));
      const source = getSource(mix);
      if (source) player.load(source);
    }
  };

  const back = () => {
    if (pointer === null) return;
    dispatchBack();
    const prevMix = history[pointer - 1];
    applyHistory(prevMix);
  };

  const forward = () => {
    if (pointer === null) return;
    dispatchForward();
    const nextMix = history[pointer + 1];
    applyHistory(nextMix);
  };

  return {
    partNumber,
    currentMix,
    play,
    pause,
    back,
    forward,
    saveSeconds,
    getSeconds,
    seek,
    hasError,
    isPlaying,
    isPaused,
    isLoading,
    isVisible,
    service: player,
  };
};

export default usePlayer;
