import { PayloadAction } from '@reduxjs/toolkit';
import { MainPlayerState } from '../mainPlayer';

const multiPartMix = (initialState: MainPlayerState) => ({
  loadNextPart(state: MainPlayerState, action: PayloadAction<number>) {
    state.partNumber = action.payload;
    state.isLoading = true;
    state.isLoaded = false;
    state.isPlaying = false;
  },
});

export default multiPartMix;
