import { MULTIPART, ONE_PART, PLAYLIST } from 'src/constants/mix';
import { Mix } from 'src/models/mix';

export const download = (fileId: string, type?: number): string | undefined => {
  switch (type) {
    case 1: {
      return `https://drive.google.com/uc?export=download&id=${fileId}`;
    }
    case 2: {
      return `https://www.dl.dropboxusercontent.com/s/${fileId}.mp3?dl=0`;
    }
  }
};

export const pdf = (fileId: string) => {
  return `https://docs.google.com/document/d/${fileId}/export?format=pdf`;
};

export const view = (fileId: string) => {
  return `https://drive.google.com/uc?export=view&id=${fileId}`;
};

export const getSource = (mix: Mix, index: number = 1): string | undefined => {
  const { sourceType, parts = [], mixType = ONE_PART } = mix;
  index = !index ? 0 : index - 1;
  switch (mixType) {
    case PLAYLIST:
    case ONE_PART:
    case MULTIPART: {
      const { sourceUrl, sourceId } = parts[index];
      return sourceUrl ? sourceUrl : download(sourceId, sourceType);
    }
  }
};
