import { configureStore, getDefaultMiddleware } from '@reduxjs/toolkit';
import container from 'src/services/container';
import rootReducer from '../@reducers';
import { thunkMiddleware, logger, apiResponseMapperMiddleware } from './middlewares';

const store = configureStore({
  reducer: rootReducer,
  middleware: [
    thunkMiddleware,
    apiResponseMapperMiddleware,
    logger as any,
    ...getDefaultMiddleware(),
  ],
});

if (process.env.NODE_ENV === 'development' && (module as any).hot) {
  (module as any).hot.accept('../@reducers', () => {
    const newRootReducer = require('../@reducers').default;
    store.replaceReducer(newRootReducer);
  });
}

// State store added to service container to use 
// store object  on some use cases.
container.singleton('store', store);

export type AppDispatch = typeof store.dispatch;
export type RootState = ReturnType<typeof store.getState>;
export type AppStore = typeof store;

export default store;
